'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

export default function DocumentsPage() {
  const [searchTerm, setSearchTerm] = useState('')
  const [filterSource, setFilterSource] = useState('all')

  const documents = [
    {
      id: 1,
      title: 'Briefing da Campanha Q1 2024',
      description: 'Documento completo com objetivos e estratégias para o primeiro trimestre',
      source: 'Google Drive',
      type: 'PDF',
      size: '2.4 MB',
      updatedAt: '2024-01-15',
      url: 'https://drive.google.com/file/d/example1',
      color: 'bg-blue-100 text-blue-800'
    },
    {
      id: 2,
      title: 'Estratégia de Redes Sociais',
      description: 'Planejamento detalhado para presença digital e engajamento',
      source: 'Google Docs',
      type: 'DOC',
      size: '1.8 MB',
      updatedAt: '2024-01-14',
      url: 'https://docs.google.com/document/d/example2',
      color: 'bg-green-100 text-green-800'
    },
    {
      id: 3,
      title: 'Templates de Design - Identidade Visual',
      description: 'Conjunto de templates para manter consistência visual da marca',
      source: 'Canva',
      type: 'DESIGN',
      size: '5.2 MB',
      updatedAt: '2024-01-12',
      url: 'https://canva.com/design/example3',
      color: 'bg-purple-100 text-purple-800'
    },
    {
      id: 4,
      title: 'Relatório de Performance - Dezembro',
      description: 'Análise completa dos resultados e métricas do mês anterior',
      source: 'Google Drive',
      type: 'PDF',
      size: '3.1 MB',
      updatedAt: '2024-01-10',
      url: 'https://drive.google.com/file/d/example4',
      color: 'bg-blue-100 text-blue-800'
    },
    {
      id: 5,
      title: 'Guia de Estilo - Comunicação',
      description: 'Diretrizes para tom de voz e linguagem da marca',
      source: 'Google Docs',
      type: 'DOC',
      size: '1.2 MB',
      updatedAt: '2024-01-08',
      url: 'https://docs.google.com/document/d/example5',
      color: 'bg-green-100 text-green-800'
    },
    {
      id: 6,
      title: 'Assets Criativos - Campanha Verão',
      description: 'Banco de imagens e elementos visuais para a campanha sazonal',
      source: 'Canva',
      type: 'DESIGN',
      size: '8.7 MB',
      updatedAt: '2024-01-05',
      url: 'https://canva.com/design/example6',
      color: 'bg-purple-100 text-purple-800'
    }
  ]

  const filteredDocuments = documents.filter(doc => {
    const matchesSearch = doc.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         doc.description.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesFilter = filterSource === 'all' || doc.source === filterSource
    return matchesSearch && matchesFilter
  })

  const handleOpenDocument = (url: string) => {
    window.open(url, '_blank')
  }

  const handleBackToDashboard = () => {
    window.location.href = '/dashboard'
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-[#F4E4F0] to-white">
      {/* Header */}
      <header className="bg-white/80 backdrop-blur-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-4">
              <Button
                onClick={handleBackToDashboard}
                variant="ghost"
                size="sm"
                className="text-[#8B3F9B] hover:bg-[#8B3F9B]/10"
              >
                ← Voltar
              </Button>
              <div className="flex items-center justify-center w-10 h-10 bg-[#8B3F9B] rounded-xl">
                <span className="text-lg font-bold text-white">lu</span>
              </div>
              <div>
                <h1 className="text-xl font-bold text-[#8B3F9B]">Documentos</h1>
                <p className="text-sm text-gray-600">Seus arquivos e materiais</p>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Page Header */}
        <div className="mb-8">
          <h2 className="text-3xl font-bold text-[#8B3F9B] mb-2">
            Seus Documentos
          </h2>
          <p className="text-gray-600">
            Acesse todos os seus arquivos organizados por fonte e tipo.
          </p>
        </div>

        {/* Filters and Search */}
        <Card className="border-0 shadow-lg bg-white/80 backdrop-blur-sm mb-8">
          <CardHeader>
            <CardTitle className="text-[#8B3F9B]">Filtros e Busca</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex flex-col sm:flex-row gap-4">
              <div className="flex-1">
                <Input
                  placeholder="Buscar documentos..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="border-gray-300 focus:border-[#8B3F9B] focus:ring-[#8B3F9B]"
                />
              </div>
              <div className="sm:w-48">
                <Select value={filterSource} onValueChange={setFilterSource}>
                  <SelectTrigger className="border-gray-300 focus:border-[#8B3F9B] focus:ring-[#8B3F9B]">
                    <SelectValue placeholder="Filtrar por fonte" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas as fontes</SelectItem>
                    <SelectItem value="Google Drive">Google Drive</SelectItem>
                    <SelectItem value="Google Docs">Google Docs</SelectItem>
                    <SelectItem value="Canva">Canva</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Documents Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredDocuments.map((doc) => (
            <Card key={doc.id} className="border-0 shadow-lg bg-white/80 backdrop-blur-sm hover:shadow-xl transition-all duration-200 cursor-pointer group">
              <CardHeader className="pb-4">
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <CardTitle className="text-[#8B3F9B] text-lg mb-2 group-hover:text-[#7A3589] transition-colors">
                      {doc.title}
                    </CardTitle>
                    <CardDescription className="text-sm text-gray-600 line-clamp-2">
                      {doc.description}
                    </CardDescription>
                  </div>
                </div>
                <div className="flex items-center space-x-2 mt-3">
                  <Badge variant="secondary" className={doc.color}>
                    {doc.type}
                  </Badge>
                  <span className="text-xs text-gray-500">{doc.source}</span>
                </div>
              </CardHeader>
              <CardContent>
                <div className="flex items-center justify-between text-sm text-gray-500 mb-4">
                  <span>{doc.size}</span>
                  <span>{new Date(doc.updatedAt).toLocaleDateString('pt-BR')}</span>
                </div>
                <Button
                  onClick={() => handleOpenDocument(doc.url)}
                  className="w-full bg-[#8B3F9B] hover:bg-[#7A3589] text-white transition-colors"
                >
                  Abrir Documento
                </Button>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Empty State */}
        {filteredDocuments.length === 0 && (
          <Card className="border-0 shadow-lg bg-white/80 backdrop-blur-sm">
            <CardContent className="text-center py-12">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                Nenhum documento encontrado
              </h3>
              <p className="text-gray-500 mb-4">
                Tente ajustar os filtros ou termos de busca.
              </p>
              <Button
                onClick={() => {
                  setSearchTerm('')
                  setFilterSource('all')
                }}
                variant="outline"
                className="border-[#8B3F9B] text-[#8B3F9B] hover:bg-[#8B3F9B] hover:text-white"
              >
                Limpar Filtros
              </Button>
            </CardContent>
          </Card>
        )}

        {/* Stats */}
        <div className="mt-8 grid grid-cols-1 sm:grid-cols-3 gap-4">
          <Card className="border-0 shadow-lg bg-white/80 backdrop-blur-sm">
            <CardContent className="text-center py-6">
              <div className="text-2xl font-bold text-[#8B3F9B] mb-1">
                {documents.length}
              </div>
              <div className="text-sm text-gray-600">Total de Documentos</div>
            </CardContent>
          </Card>
          <Card className="border-0 shadow-lg bg-white/80 backdrop-blur-sm">
            <CardContent className="text-center py-6">
              <div className="text-2xl font-bold text-[#F5B800] mb-1">
                {documents.filter(d => d.source === 'Google Drive').length}
              </div>
              <div className="text-sm text-gray-600">Google Drive</div>
            </CardContent>
          </Card>
          <Card className="border-0 shadow-lg bg-white/80 backdrop-blur-sm">
            <CardContent className="text-center py-6">
              <div className="text-2xl font-bold text-[#8B3F9B] mb-1">
                {documents.filter(d => d.source === 'Canva').length}
              </div>
              <div className="text-sm text-gray-600">Canva</div>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  )
}
